.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  flex-direction: column;
}
.wrapper__input {
  width: 200px;
  height: 30px;
  padding: 5px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
}
.wrapper__button {
  padding: 8px 16px;
  font-size: 12px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.wrapper__button:hover {
  background-color: #999;
}
.wrapper .input-button-wrapper {
  display: flex;
  gap: 10px;
  justify-content: center;
  margin-bottom: 20px;
}
.wrapper__clock {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 180px;
  align-items: center;
}
.wrapper .clock {
  border: 1px solid #ccc;
  border-radius: 50%;
  padding: 15px;
  width: 200px;
  height: 200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f5f5f5;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}
.wrapper .clock__timezone {
  margin-top: 10px;
  font-size: 16px;
}
.wrapper .clock__buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
}
.wrapper .clock .clock .clock__buttons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  margin-top: 20px;
  margin-bottom: 10px;
}
.wrapper .clock .clock .clock__button-blue {
  background-color: blue;
  color: #fff;
}
.wrapper .clock .clock .clock__button-red {
  background-color: red;
  color: #fff;
}
.wrapper .clock .clock .clock__button-green {
  background-color: green;
  color: #fff;
}
.wrapper .clock .clock .clock__buttons button {
  margin-top: 10px;
  font-size: 14px;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}
.wrapper .clock .clock .clock__buttons button:hover {
  background-color: #999;
}
.wrapper .clock p {
  font-size: 24px;
}
.wrapper .clock .clock__buttons button {
  margin-top: 10px;
  font-size: 14px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.clock .clock__buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
  bottom: 340px;
  position: absolute;
}

.clock .clock__button-blue {
  background-color: blue;
  color: #fff;
}

.clock .clock__button-red {
  background-color: red;
  color: #fff;
}

.clock .clock__button-green {
  background-color: green;
  color: #fff;
}

.clock .clock__buttons button {
  margin-top: 10px;
  font-size: 14px;
  padding: 8px 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.clock .clock__buttons button:hover {
  background-color: #999;
}